/*=================fonts=================*/
@media (max-width: 1180px) {
  .header .top-header__column .menu__list {
    grid-template-columns: 90px 110px 160px; }
  .header .top-header__column .menu__link {
    padding: 2px 0px 2px 20px; }
  .header .top-header .contacts-header__phone {
    font-size: 16px; }
  .header .top-header .contacts-header__column {
    padding: 0px 15px; }
    .header .top-header .contacts-header__column .contacts-header__item_icon {
      padding: 0px 0px 0px 25px;
      background-size: 15px; }
  .header .bottom-header .actions-header li {
    margin: 0px 2.54237vw 0px 0px; }
  .header .bottom-header .actions-header__item {
    padding-left: 30px; }
  .header .bottom-header .info-header__column {
    padding: 0px 2.54237vw 0px 0px; }
  .header .bottom-header .info-header__callback {
    background: none;
    padding-left: 0; }
  .header .bottom-header .info-header__schedule {
    background: none;
    padding-left: 0; } }

@media (max-width: 992px) {
  .header .top-header__column .icon-menu {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5; }
    .header .top-header__column .icon-menu span {
      transition: all 0.3s ease 0s;
      top: calc(50% - 1px);
      left: 0px;
      position: absolute;
      width: 100%;
      height: 2px;
      background: #ffffff; }
      .header .top-header__column .icon-menu span:first-child {
        top: 0px; }
      .header .top-header__column .icon-menu span:last-child {
        top: auto;
        bottom: 0px; }
    .header .top-header__column .icon-menu._active span {
      transform: scale(0); }
      .header .top-header__column .icon-menu._active span:first-child {
        transform: rotate(-45deg);
        top: calc(50% - 1px); }
      .header .top-header__column .icon-menu._active span:last-child {
        transform: rotate(45deg);
        top: calc(50% - 1px); }
  .header .top-header__column .menu__body {
    display: none;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    overflow: auto;
    z-index: 2;
    background: #1ab9ce;
    padding: 70px 20px 30px 20px; }
    .header .top-header__column .menu__body._active {
      display: block; }
  .header .top-header__column .contacts-header {
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .header .top-header__column .contacts-header__phone {
      font-size: 12px; }
    .header .top-header__column .contacts-header__column {
      border: none;
      padding: 0px 15px;
      width: 125px; }
      .header .top-header__column .contacts-header__column .contacts-header__item_icon {
        border-bottom: 2px solid #ffffff;
        padding: 0px;
        background: none; }
    .header .top-header__column .contacts-header__label {
      margin: 0; }
  .header .bottom-header .actions-header__item {
    padding: 0;
    background: none; }
  .header .bottom-header .info-header__schedule {
    display: none; } }

@media (max-width: 790px) {
  .header {
    background: url("../img/header-nav.jpg") 0 0 no-repeat; }
    .header .top-header__column_logo {
      flex: 0 0 100px;
      width: 100%;
      padding-top: 25px; }
      .header .top-header__column_logo img {
        max-width: 100%; }
    .header .top-header__column .contacts-header {
      align-items: flex-end; } }

@media (max-width: 640px) {
  .header {
    background: url("../img/header-nav.jpg") 0 -20px no-repeat; }
    .header .top-header__content {
      height: 70px; }
    .header .top-header__column_logo {
      padding-top: 15px; }
    .header .top-header__column .contacts-header__column {
      width: auto;
      padding: 0; }
      .header .top-header__column .contacts-header__column:last-child {
        display: none; }
      .header .top-header__column .contacts-header__column .contacts-header__item_icon {
        border: none;
        background: none; }
    .header .top-header__column .contacts-header__phone {
      font-size: 0;
      background: url("../img/icons/phone.png") left no-repeat;
      width: 19px;
      height: 25px;
      display: inline-block;
      vertical-align: middle; }
    .header .top-header__column .contacts-header__label {
      display: none; }
    .header .top-header__column .actions-header {
      display: flex;
      align-items: center; }
      .header .top-header__column .actions-header li {
        font-family: 'Roboto-Light';
        list-style: none;
        margin: 0px 54px 0px 0px; }
      .header .top-header__column .actions-header__item {
        padding-left: 36px;
        display: inline-block;
        line-height: 24px;
        text-decoration: none;
        color: #999999; }
        .header .top-header__column .actions-header__item span {
          font-size: 14px;
          border-bottom: 2px dotted #999999; }
          .header .top-header__column .actions-header__item span:hover {
            color: #1ab9ce;
            border-bottom: 2px dotted #1ab9ce; } }

@media (min-width: 992px) {
  .header .top-header__column .menu__link:hover {
    background: #068292;
    transition: all 0.3s ease 0s;
    border-radius: 20px; } }
